import { useState, useEffect } from 'react';
import { Head } from "@inertiajs/react"; // Tambahkan import ini

import pplk2025 from "!assets/pplk/2025.jpg";
import pplk2024 from "!assets/pplk/2024.jpg";
import pplk2022 from "!assets/pplk/2022.jpg";
import bg1 from "!assets/tesla/bg-3.png";
import nagaBg from "!assets/pplk/asset_naga.png";

const OpeningPage = ({ onComplete }) => {
    const [currentIndex, setCurrentIndex] = useState(0);
    const [nextIndex, setNextIndex] = useState(0);
    const [transitionDirection, setTransitionDirection] = useState<"left" | "right" | null>(null);
    const [isAnimating, setIsAnimating] = useState(false);
    const [fullscreenMode, setFullscreenMode] = useState(false);
    const [showIframe, setShowIframe] = useState(false);
    const [iframeUrl, setIframeUrl] = useState('');
    const [preloadedIframes, setPreloadedIframes] = useState({});
    const [isIframeLoading, setIsIframeLoading] = useState(true);
    const [loadedIframes, setLoadedIframes] = useState<{ [key: string]: boolean }>({});
    const [iframeReady, setIframeReady] = useState(false);
    const [activeCard, setActiveCard] = useState<'left' | 'center' | 'right' | null>(null);

    const images = [
        { id: 0, src: pplk2025, alt: 'PPLK 2025', title: 'ADVENTURE AWAITS', year: 'PPLK 2025', link: '/' },
        { id: 1, src: pplk2024, alt: 'PPLK 2024', title: 'TEAM SPIRIT', year: 'PPLK 2024', link: 'https://pplk-itera-2024-production.up.railway.app/informasi/pplk' },
        { id: 2, src: pplk2022, alt: 'PPLK 2022', title: 'LEADERSHIP', year: 'PPLK 2022', link: 'https://pplk2022-production.up.railway.app/' },
    ];

    // Function to get dynamic title based on current image
    const getDynamicTitle = () => {
        const currentYear = images[currentIndex].year;
        if (currentYear === 'PPLK 2024') {
            return 'Website PPLK 2024';
        } else if (currentYear === 'PPLK 2022') {
            return 'Website PPLK 2022';
        } else if (currentYear === 'PPLK 2025') {
            return 'Website PPLK 2025';
        }

    };

    // Function to get iframe title
    const getIframeTitle = () => {
        const currentYear = images[currentIndex].year;
        if (currentYear === 'PPLK 2024') {
            return 'PPLK 2024';
        } else if (currentYear === 'PPLK 2022') {
            return 'PPLK 2022';
        }
        return 'PPLK';
    };

    // Optimized preload effect
    useEffect(() => {
        const preloadSites = async () => {
            const externalSites = images.filter(img => img.link !== '/');
            const iframeContainer = {};

            for (const site of externalSites) {
                iframeContainer[site.link] = true;

                const iframe = document.createElement('iframe');
                iframe.style.cssText = `
                    position: fixed;
                    top: -9999px;
                    left: -9999px;
                    width: 100%;
                    height: 100%;
                    border: none;
                    opacity: 0;
                    pointer-events: none;
                    z-index: -1;
                `;
                iframe.src = site.link;

                iframe.onload = () => {
                    setLoadedIframes(prev => ({
                        ...prev,
                        [site.link]: true
                    }));
                };

                document.body.appendChild(iframe);
            }

            setPreloadedIframes(iframeContainer);
        };

        preloadSites();

        return () => {
            const preloadedFrames = document.querySelectorAll('iframe[style*="position: fixed"]');
            preloadedFrames.forEach(frame => frame.remove());
        };
    }, []);

    // Faster iframe ready state
    useEffect(() => {
        if (showIframe) {
            setIframeReady(false);
            const timer = setTimeout(() => {
                setIframeReady(true);
            }, 1000); // Reduced from 2000ms to 1000ms
            return () => clearTimeout(timer);
        }
    }, [showIframe, iframeUrl]);

    const handleImageClick = (index) => {
        if (isAnimating) return;
        let cardType: 'left' | 'center' | 'right' = 'center';
        if (index === (currentIndex + images.length - 1) % images.length) cardType = 'left';
        else if (index === (currentIndex + 1) % images.length) cardType = 'right';

        setActiveCard(cardType);

        if (index === currentIndex) {
            if (images[currentIndex].year === 'PPLK 2025') {
                if (typeof onComplete === 'function') onComplete();
            } else {
                setIsIframeLoading(true);
                setIframeUrl(images[currentIndex].link);
                setShowIframe(true);
            }
        } else {
            const total = images.length;
            setNextIndex(index); // Set target index

            if ((index === 0 && currentIndex === total - 1)) {
                setTransitionDirection('right');
            } else if ((index === total - 1 && currentIndex === 0)) {
                setTransitionDirection('left');
            } else if (index > currentIndex) {
                setTransitionDirection('right');
            } else {
                setTransitionDirection('left');
            }
            setIsAnimating(true);
        }
    };

    const exitIframe = () => {
        setShowIframe(false);
        setIframeUrl('');
    };

    const exitFullscreen = () => setFullscreenMode(false);

    const goToPrevious = () => {
        if (isAnimating) return;
        setActiveCard('left');
        const newIndex = currentIndex === 0 ? images.length - 1 : currentIndex - 1;
        setNextIndex(newIndex);
        setTransitionDirection('left');
        setIsAnimating(true);
    };

    const goToNext = () => {
        if (isAnimating) return;
        setActiveCard('right');
        const newIndex = currentIndex === images.length - 1 ? 0 : currentIndex + 1;
        setNextIndex(newIndex);
        setTransitionDirection('right');
        setIsAnimating(true);
    };

    // Animation timing with delayed index change
    useEffect(() => {
        if (isAnimating) {
            // Change index at the middle of animation (100ms out of 250ms)
            const changeTimer = setTimeout(() => {
                setCurrentIndex(nextIndex);
            }, 100);

            // Complete animation
            const completeTimer = setTimeout(() => {
                setIsAnimating(false);
                setTransitionDirection(null);
                setActiveCard(null);
            }, 250);

            return () => {
                clearTimeout(changeTimer);
                clearTimeout(completeTimer);
            };
        }
    }, [isAnimating, nextIndex]);

    // Optimized iframe render with faster transitions
    if (showIframe) {
        const isLoaded = loadedIframes[iframeUrl] && iframeReady;

        return (
            <>
                <Head title={getIframeTitle()} />
                <div className="fixed inset-0 z-50 bg-black flex flex-col justify-center">
                    <div className="absolute top-0 right-4 z-50">
                        <button
                            onClick={exitIframe}
                            className="text-white hover:text-gray-300 font-bold text-xl transition-colors duration-200"
                        >
                            ✕
                        </button>
                    </div>
                    <div className="w-full h-full relative">
                        <div
                            className={`absolute inset-0 flex flex-col items-center justify-center bg-black ${!isLoaded ? '' : 'hidden'}`}
                            style={{
                                backgroundImage: `url(${images[currentIndex].src})`,
                                backgroundSize: 'cover',
                                backgroundPosition: 'center',
                                zIndex: isLoaded ? -1 : 1,
                            }}
                        >
                            <div className="absolute inset-0 bg-black bg-opacity-75"></div>
                            <div className="relative z-10 flex flex-col items-center space-y-4">
                                <div className="w-20 h-20 border-8 border-white border-t-transparent rounded-full animate-spin"></div>
                                <p
                                    className="text-white text-2xl font-greek font-bold"
                                    style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                                >
                                    Loading {images[currentIndex].year}...
                                </p>
                            </div>
                        </div>
                        <iframe
                            key={iframeUrl}
                            src={iframeUrl}
                            className="w-full h-full border-0" // Hapus transition-opacity duration-300
                            style={{ zIndex: isLoaded ? 2 : 0, opacity: isLoaded ? 1 : 0 }}
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                            allowFullScreen
                            onLoad={() => {
                                setLoadedIframes(prev => ({
                                    ...prev,
                                    [iframeUrl]: true
                                }));
                            }}
                        />
                    </div>
                </div>
            </>
        );
    }

    return (
        <>
            <Head title={getDynamicTitle()} />
            {/* Preload iframes */}
            <div className="hidden">
                {Object.keys(preloadedIframes).map(url => (
                    <iframe
                        key={url}
                        src={url}
                        style={{ display: 'none' }}
                        aria-hidden="true"
                    />
                ))}
            </div>

            <div
                className="h-screen flex flex-col items-center justify-between relative overflow-hidden px-4 sm:px-6 py-8"
                style={{
                    backgroundImage: `url(${bg1})`,
                    backgroundSize: 'cover',
                    backgroundPosition: 'center',
                    backgroundRepeat: 'no-repeat',
                }}
            >
                {/* Background overlays */}
                <div
                    className="absolute inset-0 z-0 pointer-events-none"
                    style={{
                        backgroundImage: `url(${nagaBg})`,
                        backgroundSize: 'cover',
                        backgroundPosition: 'center',
                        backgroundRepeat: 'no-repeat',
                        opacity: 0.05,
                    }}
                />
                <div
                    className="absolute inset-0 z-10 pointer-events-none"
                    style={{
                        background: '#BF400099',
                        opacity: 1,
                        mixBlendMode: 'multiply',
                    }}
                />

                <div className="w-full max-w-7xl relative z-10 flex flex-col min-h-full py-4">
                    <div className="text-center mt-12 mb-4 md:mt-0 md:mb-12">
                        <h1
                            className="text-3xl sm:text-4xl md:text-6xl font-greek font-bold text-white"
                            style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                        >
                            SELAMAT DATANG SATRIYA
                        </h1>
                    </div>

                    <div className="relative flex items-center justify-center flex-1">
                        <div className="relative w-full max-w-5xl mx-2 sm:mx-6 flex items-center justify-center space-x-2 sm:space-x-6">
                            {/* Left Image */}
                            <div
                                className={`relative w-1/3 max-w-[100px] sm:max-w-[140px] md:max-w-[180px] aspect-[2/3] transform transition-all duration-250 ease-in-out
                                    ${isAnimating && activeCard === 'right'
                                        ? 'translate-x-full opacity-0 scale-75'
                                        : isAnimating && transitionDirection === 'left'
                                            ? 'translate-x-full opacity-0 scale-75'
                                            : isAnimating && transitionDirection === 'right'
                                                ? '-translate-x-full opacity-0 scale-75'
                                                : 'translate-x-0 opacity-80 scale-95'
                                    }
                                    ${!isAnimating ? 'hover:opacity-100 hover:scale-100' : ''}
                                    cursor-pointer`}
                                onClick={() => handleImageClick((currentIndex + images.length - 1) % images.length)}
                            >
                                <img
                                    src={images[(currentIndex + images.length - 1) % images.length].src}
                                    alt="Previous"
                                    className="w-full h-full object-cover rounded-xl shadow-xl rotate-2 transition-transform duration-300"
                                />
                                <div className="absolute inset-0 bg-black bg-opacity-40 rounded-xl transition-opacity duration-300"></div>
                                <div className="absolute inset-0 flex items-center justify-center">
                                    <h3
                                        className="font-bold text-white tracking-wide drop-shadow text-lg font-greek md:text-3xl text-center absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-full rotate-2 transition-transform duration-300"
                                        style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                                    >
                                        {images[(currentIndex + images.length - 1) % images.length].year}
                                    </h3>
                                </div>
                            </div>

                            {/* Center Image - Overlay timing and style matched to side cards */}
                            <div
                                className={`relative w-1/2 max-w-[180px] sm:max-w-[240px] md:max-w-[280px] aspect-[2/3] transform transition-all duration-250 ease-in-out z-10 group
                                    hover:scale-100 active:scale-95 touch-pan-y
                                    ${isAnimating
                                        ? (
                                            activeCard === 'right' || activeCard === 'left'
                                                ? 'scale-75 opacity-0'
                                                : (transitionDirection === 'right'
                                                    ? 'translate-x-full opacity-0 scale-75'
                                                    : '-translate-x-full opacity-0 scale-75')
                                        )
                                        : 'translate-x-0 opacity-95 scale-95'
                                    }
                                    ${!isAnimating ? 'cursor-pointer' : ''}`}
                                onClick={() => handleImageClick(currentIndex)}
                            >
                                {/* Card double - left shadow */}
                                <div
                                    className="absolute left-0 top-0 w-full h-full z-0 pointer-events-none"
                                    style={{
                                        transform: 'rotate(-1.5deg) scale(0.96) translateX(-8px)',
                                        filter: 'brightness(0.90) blur(0.5px)',
                                        opacity: 0.6,
                                    }}
                                >
                                    <img
                                        src={images[currentIndex].src}
                                        alt={images[currentIndex].alt}
                                        className="w-full h-full object-cover rounded-2xl"
                                    />
                                    {/* Border for left shadow card */}
                                    <div
                                        className="absolute inset-0 pointer-events-none"
                                        style={{
                                            border: '3px solid #FFF',
                                            borderRadius: '0.5rem',
                                            zIndex: 20,
                                            opacity: 0.10,
                                            boxShadow: `
                                                0px -8px 16px -8px rgba(255,255,255,0.15), /* top left */
                                                8px 8px 16px -8px rgba(255,255,255,0.35),  /* bottom right */
                                                -8px 8px 16px -8px rgba(255,255,255,0.15), /* bottom left */
                                                8px -8px 16px -8px rgba(255,255,255,0.15)  /* top right */
                                            `,
                                        }}
                                    ></div>
                                </div>
                                {/* Card double - right shadow */}
                                <div
                                    className="absolute left-0 top-0 w-full h-full z-0 pointer-events-none"
                                    style={{
                                        transform: 'rotate(1.5deg) scale(0.96) translateX(8px)',
                                        filter: 'brightness(0.90) blur(0.5px)',
                                        opacity: 0.6,
                                    }}
                                >
                                    <img
                                        src={images[currentIndex].src}
                                        alt={images[currentIndex].alt}
                                        className="w-full h-full object-cover rounded-2xl"
                                    />
                                    {/* Border for right shadow card */}
                                    <div
                                        className="absolute inset-0 pointer-events-none"
                                        style={{
                                            borderRadius: '0.5rem',
                                            border: '3px solid #FFF',
                                            zIndex: 20,
                                            opacity: 0.10,
                                            boxShadow: `
                                                0px -8px 16px -8px rgba(255,255,255,0.15), /* top left */
                                                8px 8px 16px -8px rgba(255,255,255,0.35),  /* bottom right */
                                                -8px 8px 16px -8px rgba(255,255,255,0.15), /* bottom left */
                                                8px -8px 16px -8px rgba(255,255,255,0.15)  /* top right */
                                            `,
                                        }}
                                    ></div>
                                </div>
                                {/* Main center card image */}
                                <img
                                    src={images[currentIndex].src}
                                    alt={images[currentIndex].alt}
                                    className="w-full h-full object-cover brightness-150 rounded-3xl transition-all duration-300 group-hover:brightness-125 relative z-10"
                                />

                                {/* Text overlay - z-index tertinggi */}
                                <div className="absolute inset-0 flex items-center justify-center z-40 pointer-events-none">
                                    <h3
                                        className="font-bold text-white tracking-wide text-2xl font-greek md:text-4xl lg:text-5xl text-center px-2"
                                        style={{
                                            textShadow: '2px 2px 8px rgba(0,0,0,0.9), 0 0 16px rgba(0,0,0,0.7), 0 0 2px rgba(0,0,0,1)',
                                            zIndex: 40,
                                            position: 'relative'
                                        }}
                                    >
                                        {images[currentIndex].year}
                                    </h3>
                                </div>
                                {/* Custom border for center card */}
                                <div
                                    className="absolute inset-0 pointer-events-none z-25"
                                    style={{
                                        border: '3px solid #FFF',
                                        borderRadius: '1.5rem',
                                        opacity: 0.10,
                                        boxShadow: `
                                            0px -8px 16px -8px rgba(255,255,255,0.15),
                                            8px 8px 16px -8px rgba(255,255,255,0.35),
                                            -8px 8px 16px -8px rgba(255,255,255,0.15),
                                            8px -8px 16px -8px rgba(255,255,255,0.15)
                                        `,
                                    }}
                                ></div>
                            </div>

                            {/* Right Image - Faster and smoother transitions */}
                            <div
                                className={`relative w-1/3 max-w-[100px] sm:max-w-[140px] md:max-w-[180px] aspect-[2/3] transform transition-all duration-250 ease-in-out
                                    ${isAnimating && activeCard === 'left'
                                        ? '-translate-x-full opacity-0 scale-75'
                                        : isAnimating && transitionDirection === 'right'
                                            ? '-translate-x-full opacity-0 scale-75'
                                            : isAnimating && transitionDirection === 'left'
                                                ? 'translate-x-full opacity-0 scale-75'
                                                : 'translate-x-0 opacity-80 scale-95'
                                    }
                                    ${!isAnimating ? 'hover:opacity-100 hover:scale-100' : ''}
                                    cursor-pointer`}
                                onClick={() => handleImageClick((currentIndex + 1) % images.length)}
                            >
                                <img
                                    src={images[(currentIndex + 1) % images.length].src}
                                    alt="Next"
                                    className="w-full h-full object-cover rounded-xl shadow-xl -rotate-2  transition-transform duration-300"
                                />
                                <div className="absolute inset-0 bg-black bg-opacity-40 rounded-xl transition-opacity duration-300"></div>
                                <div className="absolute inset-0 flex items-center justify-center">
                                    <h3
                                        className="font-bold text-white tracking-wide drop-shadow text-lg font-greek md:text-3xl text-center absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-full -rotate-2 transition-transform duration-300"
                                        style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                                    >
                                        {images[(currentIndex + 1) % images.length].year}
                                    </h3>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="flex flex-col sm:flex-row items-center justify-between gap-8 sm:gap-4 text-white px-2 mt-auto pt-4 pb-2 sm:pb-8">
                        <div className="flex items-center gap-2 text-lg font-greek md:text-3xl order-1 sm:order-1">
                            <span
                                className="text-2xl font-bold transition-all duration-300"
                                style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                            >
                                0{currentIndex + 1}
                            </span>
                            <span
                                className="text-gray-400"
                                style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                            >/</span>
                            <span
                                className="text-gray-300"
                                style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                            >0{images.length}</span>
                        </div>

                        <div className="flex justify-center w-full sm:w-auto sm:absolute sm:left-1/2 sm:transform sm:-translate-x-1/2 space-x-2 order-3 sm:order-2">
                            {images.map((_, index) => (
                                <button
                                    key={index}
                                    onClick={() => {
                                        if (!isAnimating && index !== currentIndex) {
                                            const total = images.length;
                                            setNextIndex(index);

                                            // Tentukan arah dan set activeCard agar animasi konsisten
                                            if ((index === 0 && currentIndex === total - 1)) {
                                                setTransitionDirection('right');
                                                setActiveCard('right');
                                            } else if ((index === total - 1 && currentIndex === 0)) {
                                                setTransitionDirection('left');
                                                setActiveCard('left');
                                            } else if (index > currentIndex) {
                                                setTransitionDirection('right');
                                                setActiveCard('right');
                                            } else {
                                                setTransitionDirection('left');
                                                setActiveCard('left');
                                            }
                                            setIsAnimating(true);
                                        }
                                    }}
                                    className={`w-3 h-3 rounded-full transition-all duration-200 ${index === currentIndex ? 'bg-white scale-125' : 'bg-white bg-opacity-50 hover:bg-opacity-75'}`}
                                    style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                                />
                            ))}
                        </div>

                        <div className="flex items-center space-x-4 text-base sm:text-lg text-gray-200 font-greek md:text-3xl order-2 sm:order-3">
                            <button
                                type="button"
                                onClick={goToPrevious}
                                disabled={isAnimating}
                                className="hover:underline focus:outline-none transition-colors duration-200 hover:text-white disabled:opacity-50"
                                style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                            >
                                PREVIOUS
                            </button>
                            <span className="mt-2" style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}>|</span>
                            <button
                                type="button"
                                onClick={goToNext}
                                disabled={isAnimating}
                                className="hover:underline focus:outline-none transition-colors duration-200 hover:text-white disabled:opacity-50"
                                style={{ textShadow: '0 1px 2px rgba(0,0,0,0.7)' }}
                            >
                                NEXT
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    );
};

export default OpeningPage;
